import { useState } from 'react';
import './App.css';
import {encryptPin} from './getPin'

function App() {
  const [pin, setPin] = useState('4252')
  const [encodedPin, setEncodedPin] = useState('')

  const doTheThing = () => {
    async function a() {
      setEncodedPin(await encryptPin(pin))
    }
    
    a()
  }

  return (
    <div className="App" style={{ marginTop: '15px'}}>
      <input value={pin} onChange={(e) => { setPin(e.currentTarget.value)}}/>
      <button onClick={doTheThing}>Encode</button>
      <hr style={{marginTop: '15px'}}/>
      <p>{encodedPin}</p>
    </div>
  );
}

export default App;
